.country-card-main{
    width: 100%;
    height: 100%;
    padding: 3%;
    background: white;
    border-radius: 5px;
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
}
.country-name{
    width: 100%;
    height: 15%;
    font-weight: bold;
    font-size: 1.2vw;
    text-align: start;
    color: #2196F3;
    font-family: 'Montserrat', sans-serif;
    margin-bottom: 8%;
}
.info-body{
    width: 100%;
    height: 75%;
}
.country-flag{
    width: 30%;
    height: 100%;
    float: left;
}
.country-info{
    width: 70%;
    height: 100%;
    float: left;
    font-size: 0.9vw;
}
.sub-country-info-type{
    text-align: start;
    width: 30%;
    float: left;
}
.sub-country-info-value{
    text-align: start;
    width: 70%;
    float: left;
}
.flag-img{
    padding-left: 10%;
    padding-right: 10%;
}
.info-class{
    width: 100%;
}
.home-main{
    width: 100%;
    height: 100%;
    padding: 1%;    
}
.divider{
    height: 10px solid #ffffff;
    width: 100%;
}
.country-body-div{
    width: 100%;
    height: 90%;
    max-height: 90%;
    overflow-y: scroll;
}
.filter-box-div{
    width: 100%;
    height: 10%;
}
.filter-box-main{
    width: 24%;
    height: 100%;
    background: white;
    margin-right: 1%;
    border-radius: 5px;
    float: left;
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
}
.search-box-div{
    margin-left: 2%;
    width: 95%;
    height: 100%;
    float: left;
}
.country-card-block{
    width: 23%;
    height: 20%;
    float: left;
    margin: 1%;
}
.App {
  width: 100%;
  height: 100%;
  max-width: 100%;
  max-height: 100%;
  overflow-y: scroll;
  background: #CFD8DC;  /* fallback for old browsers */
  text-align: center;
  pointer-events: auto;
}
Router{
  width: 100%;
  height: 100%;
}
.navbar-section{
  width: 100%;
  height: 10%;
}
.pages{
  width: 100%;
  height: 90%;
}
.main{
  width: 100%;
  height: 100%;
}
.App-link {
  color: #61dafb;
}
#overlay {
  padding-top:20%;
  display: inline-block;
  position: fixed;
  display: none;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0,0,0,0.3);
  z-index: 2;
  cursor: pointer;
}
